import React, { useState } from 'react';
import { SideMenu, ItemText, Divider, ItemWrapper, ItemIcon, SelectedBar } from './Menu.styles';
import EarthIcon from '../../assets/icons/globe-americas.svg';
import UserCircle from '../../assets/icons/user-circle.svg';
import PlusCircle from '../../assets/icons/plus-circle.svg';
import FileContract from '../../assets/icons/file-contract.svg';
import UserHeadset from '../../assets/icons/user-headset.svg';

const MenuItem = (props: { icon: any; selected?: boolean; children: any; style?: any }) => {
  const { icon, selected, children, ...rest } = props;

  return (
    <ItemWrapper selected={props.selected} {...rest}>
      {props.selected && <SelectedBar />}
      {icon && <ItemIcon src={icon} />}
      <ItemText>{props.children}</ItemText>
    </ItemWrapper>
  );
};

const Menu: React.FC = () => {
  return (
    <SideMenu>
      <MenuItem icon={EarthIcon} selected={true}>
        Resumo Geral
      </MenuItem>
      <Divider />
      <MenuItem icon={UserCircle}>Meus investimentos</MenuItem>
      <Divider />
      <MenuItem icon={FileContract}>Meus contratos</MenuItem>
      <Divider />
      <MenuItem icon={PlusCircle}>Novo investimento</MenuItem>

      <span style={{ position: 'absolute', bottom: '0', width: '100%' }}>
        <Divider />
        <MenuItem icon={UserHeadset}>Atendimento</MenuItem>
      </span>
    </SideMenu>
  );
};

export default Menu;
