
// Recebe uma string numérica com ponto e vírgula e inverte virgula por ponto (padrão brasileiro)
export const InvertCommaAndPeriod = (value: string) => {
    const placeholder = 'XYZABC'
    let newValue = value.replace(',', placeholder)
    newValue = newValue.replace('.', ',')
    newValue = newValue.replace(placeholder, '.')
    return newValue
}